.indicators {
  list-style: none;
  margin-inline-start: 15px;
  margin-inline-end: 15px;
  padding-inline-start: 0;
  text-align: left;
}

.indicators__indicator {
  position: relative;
  min-height: 24px;
}

.indicators__status--connected {
  color: #43F58B;
}

.indicators__status--disconnected {
  color: #F56E43;
}